@import url(https://kit.fontawesome.com/2b86d1c003.js);
.nav {
    background-color: var(--heron);
    color: var(--light);
}

.nav li :hover {
    background-color: var(--yellow);
    color: var(--heron);
    border: solid 1px var(--light) !important;
}

.nav li a {
    color: var(--light) !important;
}

.active {
    background-color: var(--yellow) !important;
    color: var(--heron) !important;
    border: solid 1px var(--light) !important;

}

.nav-link :active {
    color: var(--heron) !important;
    border: solid 1px var(--light) !important;
}

.about {
    color: var(--heron);
    font-family: "Sentinel A, Sentinel B, Georgia, Helvetica Neue, Arial, sans-serif";
    /* background-color: #f8faf0; */
}

.about-title {
    font-size: 75px;
}

.list {
    font-size: 25px;
}
.projects {
    color: var(--heron);
    font-family: "Sentinel A, Sentinel B, Georgia, Helvetica Neue, Arial, sans-serif";
}

.cs-card {
    background-color: var(--light) !important;
    border: solid 1px var(--heron) !important;
}

.btn-card {
    background-color: var(--heron) !important;
    color: var(--light) !important;
    border: solid 2px var(--heron) !important;
}

.list-group-item {
    background-color: var(--light) !important;
    color: var(--heron) !important;
}
.contact {
  font-family: "Sentinel A","Sentinel B","Georgia","Helvetica Neue",Arial,sans-serif;
  color: var(--heron);
}

.contact-info {
  list-style: none;
}

.contact-link {
  text-decoration: none;
  /* color: var(--heron); */
}

li a.contact-link :hover{
  color: var(--yellow) !important;
}

.contact-btn {
  background-color: var(--heron) !important;
  border: solid 2px var(--heron) !important;
  color: var(--light) !important;
}

.btn:hover {
  background-color: var(--yellow) !important;
}

#nameError {
    display: none;
    font-size: 0.8em;
  }
  
  #nameError.visible {
    display: block;
  }
  
  input.invalid {
    border-color: var(--yellow);
  }
 .resume {
    color: var(--heron);
    font-family: "Sentinel A, Sentinel B, Georgia, Helvetica Neue, Arial, sans-serif";
}

.font-italic {
    font-style: italic;
}

.font-weight-bold {
    font-weight: bold;
}

.resume-btn {
    background-color: var(--heron) !important;
    border: solid 2px var(--heron) !important;
    color: var(--light) !important;
}

.resume-link {
    color: var(--light) !important;
    text-decoration: none;
    background-color: transparent;
}

.resume-link :hover {
    background-color: var(--yellow) !important;
    color: var(--light) !important;
}


:root {
    --heron: #446389;
    --light: #f8faf0;
    --yellow: #FFC47B;
    --grayBlue: #b5d2dd;
  }

.header {
    background-color: #b5d2dd;
    background-color: var(--grayBlue);
    color: #f8faf0;
    color: var(--light);
    font-family: "Sentinel A, Sentinel B, Georgia, Helvetica Neue, Arial, sans-serif";
}

.titleStyle {
    font-size: 4em;
    font-family: "Sentinel A","Sentinel B","Georgia","Helvetica Neue",Arial,sans-serif;
} 

.subtitleStyle {
    font-size: 30px;
    color: #446389;
    color: var(--heron);
}

.footer-link {
    color: #b5d2dd;
    color: var(--grayBlue);
    text-decoration: none;
    font-size: 25px;
}

#footer {
    position: absolute;
    bottom: 0;
    min-width: 100vw;
    height: 2.5rem;  
    color: #f8faf0;  
    color: var(--light);
    font-family: "Sentinel A","Sentinel B","Georgia","Helvetica Neue",Arial,sans-serif;
    background-color: #446389;
    background-color: var(--heron);
}

#page-container {
    position: relative;
    min-height: 100vh;
    max-width: 100vw;
    background-color: #f8faf0;
    background-color: var(--light);
}

#content-wrap {
    padding-bottom: 2.5rem;
    background-color: #f8faf0;
    background-color: var(--light);
}
#root {
    /* position: relative;
    min-height: 100vh;
    min-width: 100vw; */
    background-color: var(--light);
}


